.container{
    height: 60px;
    width: 100%;
    padding: 0.25rem 2rem;
}

.wrapper{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.left{
    text-decoration: none;
    font-size: 20px;
    font-weight: bold;
    color: #dc9716;

}

.right{
    display: flex;
    align-items: center;
    background-color: #efefef;
    padding: 0.5rem;
    border-radius: 7px;
}

.right > input{
    outline: none;
    border: none;
    background-color: #efefef;
    padding: 0 0.25rem;
    transition: 150ms all;
}

.right > input:hover{
    padding-left: 0.75rem;
}